.button {
  display: block;
  font-weight: 700;
  font-size: 18px;
  line-height: 18px;
  box-sizing: border-box;
  color: #1d2e5b;
  margin: 0;
  padding: 18px 40px 19px 20px;
  position: relative;
  text-decoration: none;
  background-color: #ffd74b;
  border: 3px solid transparent;
  border-radius: 10px;
  cursor: pointer;
  outline: none;
}
@media (min-width: 768px) {
  .button {
    font-size: 24px;
    line-height: 24px;
    padding: 25px 30px;
    border-radius: 20px;
  }
}
.button:hover,
.button:focus {
  background-color: #ffbd30;
}
.button:active {
  color: rgba(29, 46, 91, 0.3);
}
.button:active::after {
  opacity: 0.3;
}
.button::after {
  content: "";
  position: absolute;
  top: 50%;
  right: 17px;
  width: 13px;
  height: 13px;
  background-image: url("../img/polygon.svg");
  background-repeat: no-repeat;
  margin: -7px 0 0 0;
}
@media (min-width: 768px) {
  .button::after {
    right: 30px;
    margin-top: -6px;
  }
}
.button--border {
  border: 3px solid #192144;
}
@media (min-width: 768px) {
  .button--border {
    border-radius: 20px;
  }
}
.button--border:hover,
.button--border:focus {
  background-color: #192144;
  color: #ffffff;
}
.button--border:hover::after {
  background-image: url("../img/polygon-active.svg");
}
.button--border:active {
  color: rgba(255, 255, 255, 0.3);
}
.button--border:active::after {
  background-image: url("../img/polygon-active2.svg");
}
