.user-card {
  padding: 0;
  list-style-type: none;

  @media (min-width: @tablet-width) {
    padding: 0 34px;
    display: flex;
    justify-content: space-between;
  }

  @media (min-width: @desktop-width) {
    grid-column: 2/3;
    grid-row: 1/6;
    justify-content: flex-end;
    padding: 9px 32px 0 0;
  }
}

.user-card__wrapper-img {
  width: 270px;
  height: 270px;
  border-radius: 15px 15px 0 0;
  outline: none;

  @media (min-width: @tablet-width) {
    width: 285px;
    height: 285px;
    overflow: hidden;
    border-radius: 20px 20px 0 0;
  }

  &:focus .user-card__avatar {
    transform: scale(1.1);
    transition: 1s;
  }
}

.user-card__item {
  display: flex;
  padding: 0 0 14px 0;
  flex-direction: column;
  align-items: center;
  background-color: #ffffff;
  position: relative;
  border-radius: 15px;
  outline: none;

  @media (min-width: @tablet-width) {
    max-width: 285px;
    padding-bottom: 20px;
    border-radius: 20px;
  }

  &:hover,
  &:focus {
    box-shadow: 0 4px 9px rgba(0, 0, 0, 0.16);
  }

  &:hover .user-card__avatar,
  &:focus .user-card__avatar {
    transform: scale(1.1);
    transition: 1s;
  }
}

.user-card__item:not(:first-child) {
  margin-top: 9px;

  @media (min-width: @tablet-width) {
    margin-top: 0;
  }

  @media (min-width: @desktop-width) {
    margin-left: 60px;
  }
}

.user-card__avatar {
  display: block;
  width: 100%;
  height: 270px;
  border-radius: 15px 15px 0 0;
  transition: 1s;

  @media (min-width: @tablet-width) {
    height: 285px;
    border-radius: 20px 20px 0 0;
  }
}

.user-card__mark {
  width: 60px;
  height: 60px;
  position: absolute;
  top: 18px;
  left: 18px;
  box-sizing: border-box;

  @media (min-width: @tablet-width) {
    top: 21px;
    left: 21px;
  }
}

.user-card__circle {
  transform: rotate(-90deg);
  stroke: #ff8d30;
  fill: #000000;
  fill-opacity: 0.7;
}

.user-card__mark b {
  position: absolute;
  z-index: 1;
  left: 50%;
  transform: translateX(-50%);
  width: 30px;
  margin: 13px auto 0 auto;
  font-weight: 700;
  font-size: 24px;
  line-height: 24px;
  text-align: center;
}

.user-card__mark b::after {
  content: "";
  position: absolute;
  bottom: 0;
  left: 0;
  width: 30px;
  height: 1px;
  background-color: rgba(255, 255, 255, 0.3);
}

.user-card__mark span {
  position: absolute;
  z-index: 1;
  top: 38px;
  left: 50%;
  transform: translateX(-50%);
  font-weight: 400;
  font-size: 14px;
  line-height: 14px;
}

.user-card__counries {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  margin: -10px 0 0 19px;
  padding: 0 10px;
}

.user-card__counries img {
  display: block;
  margin-bottom: 5px;
}

.user-card__wrapper-name {
  margin-top: 6px;
  padding: 0 10px;
  display: flex;
  align-items: center;
  position: relative;
  outline: none;

  @media (min-width: @tablet-width) {
    margin-top: 9px;
    align-self: flex-start;
    margin-left: 57px;
  }

  &:hover,
  &:focus {
    opacity: 0.7;
  }

  &:active {
    opacity: 0.3;
  }
}

.user-card__status {
  display: block;
  position: absolute;
  width: 6px;
  height: 6px;
  top: 50%;
  left: -6px;
  background-color: #f97676;
  border-radius: 50%;
  margin: -2px 0 0 0;

  @media (min-width: @tablet-width) {
    width: 8px;
    height: 8px;
    left: -11px;
  }
}

.user-card__user-name {
  margin: 0;
  padding: 0;
  display: flex;
  justify-content: center;
}

.user-card__user-name a {
  display: block;
  font-weight: 700;
  font-size: 21px;
  line-height: 21px;
  color: #1d2e5b;
  text-decoration: none;
  padding: 0;
  outline: none;

  @media (min-width: @tablet-width) {
    font-size: 24px;
    line-height: 24px;
  }

  &:focus {
    opacity: 0.7;
  }
}

.user-card__meta {
  padding: 0 46px;

  @media (min-width: @tablet-width) {
    margin: 24px 0 0 0;
    padding: 0 30px;
  }
}

.user-card__icons {
  display: flex;
  padding: 0 30px;
  flex-wrap: wrap;
  justify-content: center;

  @media (min-width: @tablet-width) {
    margin: 30px 0 0 0;
  }
}

.user-card__wrapper-icon,
.user-card__wrapper-countries {
  position: relative;
}

.user-card__wrapper-icon:not(:last-child) {
  margin-right: 12px;
}

.user-card__wrapper-icon:hover::before,
.user-card__wrapper-countries:hover::before {
  @media (min-width: @desktop-width) {
    content: attr(data-tooltip);
    display: block;
    position: absolute;
    top: 50%;
    font-weight: 400;
    font-size: 20px;
    line-height: 20px;
    color: #ffffff;
    padding: 25px 25px 18px 25px;
    margin: 20px 0 0 0;
    background-color: #101d41;
    border-radius: 4px;
  }
}

.user-card__wrapper-icon:hover::after,
.user-card__wrapper-countries:hover::after {
  @media (min-width: @desktop-width) {
    content: "";
    display: block;
    width: 16px;
    height: 10px;
    position: absolute;
    bottom: -6px;
    left: 50%;
    margin: 0 0 0 -8px;
    box-sizing: border-box;
    background-image: url("../img/tooltype.png");
    background-repeat: no-repeat;
    background-position: center bottom;
  }
}

.user-card__icon {
  fill: #1d2e5b;
  opacity: 0.15;
  position: relative;
}


.user-card__icon--active {
  fill: #101d41;
  opacity: 1;
}

.user-card__icon-description {
  display: none;
}

.user-card__wrapper-icon:hover,
.user-card__wrapper-countries:hover {
  display: flex;
  flex-direction: column;
  align-items: center;
  position: relative;
}

.user-card__wrapper-countries {
  margin-right: 20px;
  box-sizing: border-box;
}

.user-card__wrapper-icon:hover .user-card__icon-description,
.user-card__wrapper-countries:hover .user-card__icon-description {
  @media (min-width: @desktop-width) {
    display: block;
    position: absolute;
    top: 50%;
    font-weight: 400;
    font-size: 20px;
    line-height: 20px;
    color: #ffffff;
    padding: 25px 25px 18px 25px;
    margin: 20px 0 0 0;
    background-color: #101d41;
    border-radius: 4px;
  }
}

.user-card__icon-description::before {
  content: "";
  display: block;
  width: 16px;
  height: 10px;
  position: absolute;
  top: -9px;
  left: 50%;
  margin: 0 0 0 -8px;
  box-sizing: border-box;
  background-image: url("../img/tooltype.png");
  background-repeat: no-repeat;
  background-position: center bottom;
}
